import { getAnimeDetail } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Play, Heart, Share2, Star, Calendar, Film, Activity } from 'lucide-react'
import { notFound } from 'next/navigation'

interface AnimePageProps {
  params: Promise<{ id: string }>
}

export default async function AnimePage({ params }: AnimePageProps) {
  const { id } = await params
  const animeId = parseInt(id)
  if (isNaN(animeId)) notFound()

  let anime = null
  try {
    anime = await getAnimeDetail(animeId)
  } catch (err) {
    console.error('Error fetching anime:', err)
    notFound()
  }

  if (!anime) notFound()

  const imageUrl = anime.images?.jpg?.large_image_url || '/placeholder.svg'
  const titleEnglish = anime.title_english || '-'
  const score = anime.score != null ? anime.score.toFixed(1) : '-'
  const episodes = anime.episodes ?? 'TBA'
  const status = anime.status || '-'
  const year = anime.year ?? 'TBA'
  const genres = anime.genres ?? []
  const synopsis = anime.synopsis || '-'
  const airedFrom = anime.aired?.from ? new Date(anime.aired.from).toLocaleDateString() : 'TBA'
  const airedTo = anime.aired?.to ? new Date(anime.aired.to).toLocaleDateString() : 'Ongoing'

  return (
    <main className="min-h-screen bg-background overflow-x-hidden">

      <div className="fixed top-0 left-0 w-full z-50">
        <Navbar />
      </div>

      {/* HERO BACKGROUND */}
      <div className="relative h-112 md:h-128 w-full overflow-hidden">
        <Image
          src={imageUrl}
          alt="Anime background"
          fill
          className="object-cover blur-sm"
          quality={80}
        />

        {/* FIXED — tidak hitam pekat di dark mode */}
        <div className="absolute inset-0 bg-gradient-to-t 
          from-black/40 dark:from-black/20 
          via-transparent to-transparent">
        </div>
      </div>

      {/* CONTENT WRAPPER */}
      <div className="relative z-20 max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 -mt-24 md:-mt-40 grid grid-cols-1 md:grid-cols-3 gap-8">

        {/* POSTER */}
        <div className="relative w-44 md:w-60 mx-auto md:mx-0">
          <div className="shadow-2xl rounded-lg overflow-hidden group transition-transform duration-500 hover:scale-105">
            
            <Image
              src={imageUrl}
              alt={anime.title}
              width={300}
              height={400}
              className="w-full h-auto"
            />

            {/* FIXED — hover tidak menutup hitam */}
            <div className="absolute inset-0 bg-black/10 group-hover:bg-black/30 transition-all"></div>

            <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity">
              <div className="w-14 h-14 bg-primary/80 rounded-full flex items-center justify-center">
                <Play size={24} className="text-white" />
              </div>
            </div>

          </div>
        </div>

        {/* RIGHT TEXT SECTION */}
        <div className="md:col-span-2 text-center md:text-left flex flex-col justify-center gap-4">
          <h1 className="text-3xl md:text-4xl font-bold text-white drop-shadow-lg">
            {anime.title}
          </h1>

          <p className="text-white/80 text-lg drop-shadow-sm">{titleEnglish}</p>

          {/* STATS GRID */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-4">

            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Star className="text-yellow-400" />
              <span className="text-sm text-muted-foreground">Rating</span>
              <span className="text-lg font-bold text-primary">{score}</span>
            </div>

            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Film className="text-blue-500" />
              <span className="text-sm text-muted-foreground">Episodes</span>
              <span className="text-lg font-bold text-primary">{episodes}</span>
            </div>

            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Activity className="text-green-500" />
              <span className="text-sm text-muted-foreground">Status</span>
              <span className="text-lg font-bold text-primary">{status}</span>
            </div>

            <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
              <Calendar className="text-purple-500" />
              <span className="text-sm text-muted-foreground">Year</span>
              <span className="text-lg font-bold text-primary">{year}</span>
            </div>

          </div>

          {/* ACTION BUTTONS */}
          
        </div>
      </div>

      {/* GENRES */}
      {genres.length > 0 && (
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mt-12 mb-6 flex flex-wrap gap-3">
          {genres.map((genre) => (
            <span
              key={genre.mal_id}
              className="px-3 py-1 bg-card border border-primary text-primary text-sm rounded-full"
            >
              {genre.name}
            </span>
          ))}
        </div>
      )}

      {/* SYNOPSIS */}
      <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
        <h2 className="text-2xl font-bold text-foreground mb-4">Synopsis</h2>
        <p className="text-muted-foreground leading-relaxed">{synopsis}</p>
      </section>

      {/* AIR DATES */}
      <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
        <h2 className="text-2xl font-bold text-foreground mb-4">Air Dates</h2>
        <p className="text-muted-foreground">{airedFrom} - {airedTo}</p>
      </section>

    </main>
  )
}
